<template>
  <div class="wap-main">
    <div class="wap-main-title">
      <div class="text">
        在线聊天室
        <Icon type="md-arrow-dropdown" @click="chatroom_active=!chatroom_active"
              size="24"/>
        <div class="wap-create-group-modal" v-show="chatroom_active">
          <ul>
            <li @click="$router.push('/group_add')" v-if="user && user.type < 2">创建群组</li>
            <li @click="$router.push('/friend_add')">添加好友</li>
          </ul>
        </div>
      </div>
      <!--<div class="more">-->
      <!--<Icon type="md-more" size="30"/>-->
      <!--</div>-->
    </div>
    <div class="wap-main-bottom">
      <ul>
        <li class="wap-main-bottom-chat active">
          <div>
            <Icon type="ios-chatbubbles" size="32"/>
          </div>
          <p>聊天</p>
        </li>
        <li @click="toFriend" style="width: 23%;">
          <div>
            <Icon type="md-contacts" size="32"/>
          </div>
          <p>
            好友
          </p>
        </li>
        <li @click="toGroup" style="width: 23%;">
          <div>
            <Icon type="ios-people" size="34"/>
          </div>
          <p>
            群聊
          </p>
        </li>
        <li class="wap-main-bottom-my" @click="toUser">
          <div>
            <Icon type="md-person" size="32"/>
          </div>
          <p>我</p>
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
  import {checkLogin, Logout, updateUser, uploadLogo} from "../../api";

    export default {
        name: "Tab",
      created(){
        this.checkLogin();
      },
      data(){
          return{
            active: 'chat',
            user: {},

            /*--------  用户消息相关属性  --------*/
            send_image: null,               // 选择发送图片
            setting_active: false,          // 用户设置框

            chat_setting_show: null,       // 用户设置
            del_friend_modal: false,       // 删除好友
            edit_remark_modal: false,      // 修改备注
            new_remark_name: '',            // 新备注名
          }
      },
      methods:{
        toMenu(menu){
          this.active = menu;
          this.$router.push('/pc/' + menu)
        },
        // 跳转到聊天页面
        toChat() {
          this.$router.push('/wap');
          this.active = 'chat'
        },

        // 跳转到好友页面
        toFriend() {
          this.$router.push('/friend');
          this.active = 'friend'
        },

        // 跳转到群组页面
        toGroup() {
          this.$router.push('/group');
          this.active = 'group'
        },

        // 跳转到个人设置
        toUser() {
          this.$router.push('/user');
          this.active = 'user'
        },



        async checkLogin(){
          let resp = await checkLogin();
          console.log(resp);
          if(resp.code === 200){
            this.$User.setUser(resp.data);
            this.user = resp.data;
            // this.$socket.emit('in_chat');
          }else{
            this.$Message.warning(resp.message);
          }

        },




      },
      watch:{
        // 监听URL状态改变
        "$route"(){
          let path = this.$route.path;
          console.log('path:',path);
          this.active = path.split('/')[path.split('/').length - 1]
        }
      }
    }
</script>

<style scoped>

</style>



